.table-container {
  margin: 15px 0;
  flex: 1;
  display: flex;
  flex-direction: column;
  height: 100%;
  overflow-y: hidden;
  text-align: center;
  td {
    vertical-align: middle;
  }
  .table-body-container {
    flex: 1;
    overflow-y: auto;

    .table-body-scroller {
      .table-action {
        height: 100%;
        vertical-align: center;
        cursor: pointer;
      }
    }
  }
  .pagination {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding: 25px 0 0 25px;
    .table-pagination-info {
      padding-left: 25px;
    }
  }
}

th {
  vertical-align: middle !important;
  background-color: #fff;
  position: sticky;
  top: 0; /* Don't forget this, required for the stickiness */
}
@media screen and (max-width: 599px) {
  .table-container {
    .pagination {
      background-color: #fff;
      height: 40px;
      .table-pagination-info {
        font-size: 14px;
      }
    }
  }
}
